import {Directive, ElementRef, Renderer, Optional} from '@angular/core';
import {NglPill} from './pill';


export class NglPillLink {

  constructor( pill: NglPill, element: ElementRef, renderer: Renderer) {
    if (!pill) return;

    renderer.setElementClass(element.nativeElement, 'slds-pill__label', true);
    pill.unlinked = false;
  }

static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{
  selector: 'a',
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: NglPill, decorators: [{ type: Optional }, ]},
{type: ElementRef, },
{type: Renderer, },
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
